.container{
    position: absolute;
    display: flex;
    width: 600px;
    height: 500px;
    justify-content: center;
    
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    }
        
    .back{
    display: flex;
    position: absolute;
    width: 600px;
    height: 500px;
    background-color: transparent;
    z-index: -1;
    }
    
    .front_container{
        width: 100%;
        height: 100%;
        overflow: hidden;
        position: absolute;
    }
    
    .front{
    position: absolute;
    width: 600px;
    height: 225px;
    background-color: #EEC048;
    
    box-shadow: 0px 0px 7px 0px rgba(0,0,0,0.5);
    z-index: 2;
    bottom: 0;
    }
    
    
    
    
    .topTriangle{
        width: 0;
        height: 0;
        border-left: 300px solid transparent;
        border-right: 300px solid transparent;
        border-bottom: 275px solid #EEC048;
        align-self: flex-start;  
    }
    
    
    .card{
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 540px;
    height: 240px;
    border-radius: 4px;
    background-color: #546E7A;
    transition: bottom 0.5s ease 0s;
    bottom: 10%;
    z-index: 1;
    text-align: center;
    }
    
    .title1{
    color: #E57373;
    font-size: 60px;
    bottom: 20px;
    }
    
    .desc{
        color: white;
        font-size: 50px;
    }
    
    .card:hover{
    bottom: 45%;
      cursor:pointer
    }